/**
 *************************************** General variables
 */
@font-face 
  font-family: Play
  src: url("../fonts/Play-Regular.woff2") format("woff2")
  src: url("../fonts/Play-Regular.woff") format("woff")
  font-style: normal
  font-weight: normal

@font-face 
  font-family: Esqadero
  src: url("../fonts/Esqadero FF CY 4F-Regular.woff2") format("woff2")
  src: url("../fonts/Esqadero FF CY 4F-Regular.woff") format("woff")
  font-style: normal
  font-weight: normal

@font-face 
  font-family: Jura
  src: url("../fonts/Jura.woff2") format("woff2")
  src: url("../fonts/Jura.woff") format("woff")
  font-style: normal
  font-weight: normal

$bdrds: 3px

$white: #fff
$black: #000
$gray: #888
$salmon: deeppink
$smoky-black: rgba(#222222, .85)
$darksmoky-black: rgba(#111111, .9)

$ff: 'Esqadero', sans-serif
$ff-text: 'Jura', sans-serif
$ff-body: 1rem
$ff-light: 300
$ff-regular: 400
$ff-medium: 500

$h-header: 3rem
$h-footer: 3rem
$img-width: 13rem

/**
 *************************************** General configs
 */
*
  box-sizing: border-box
  margin: 0
  padding: 0
  color: $white

body
  font-family: $ff
  font-size: $ff-body
  line-height: 1em
  
button
  background-color: transparent
  padding: 0
  border: 0
  outline: 0
  cursor: pointer

header
  flex: 0 0 $h-header
  line-height: $h-header
  background: $smoky-black
  text-transform: uppercase
  text-align: right
  padding: 0 1rem

footer
  height: $h-footer
  width: 100vw
  line-height: $h-footer
  background: $darksmoky-black
  text-transform: uppercase
  text-align: center
  float: right
  p
    color: $gray
    font-size: calc(#{$h-footer} / 5)
    letter-spacing: .4rem

.wrap
  width: 100%
  height: calc(100vh - #{$h-header})
  background: $black
  display: flex
  flex-flow: row nowrap

/**
 *************************************** Left image style
 */
.laside
  flex: 0 0 8rem
  position: relative
  display: flex
  flex-flow: column nowrap
  justify-content: space-between
  align-items: center
  &_img
    position: absolute
    top: 0
    left: 0
    background-image: url(../img/img1.jpg)
    background-size: cover
    background-position: 0% 50%
    height: 100%
    width: 100%
    transition: opacity 4s linear, background-position 20s linear

.profile
  width: 5rem
  height: 5rem
  background: pink
  border-radius: 50%
  margin: 1rem 0 0 0
  &_img
    width: 100%
    height: 100%
    border-radius: 50%

.settings_img
  width: 3rem
  height: 3rem
  margin: 0 0 1rem 0
  filter: invert(100%)
  &:hover
    cursor: pointer

/**
 *************************************** logo description
 */
.logo
  position: absolute
  top: 1.1rem
  left: 9rem
  z-index: 1
  font-face: $ff
  font-size: 2rem
  font-weight: bold
  mix-blend-mode: normal

  &_text
    color: $salmon
    text-shadow: 0px 0px 25px rgb(255, 255, 255)

/**
 *************************************** Page background
 */
.main
  flex: 1 1 auto
  display: flex
  flex-flow: column nowrap
  height: 100%

.main_container
  display: flex
  justify-content: flex-start
  align-items: center
  width: 100%
  height: 100%
  background: #ccc
  background-size: cover

/**
 *************************************** main window
 */
.clients, .chat, .templates
  height: 100%
  
.clients
  flex: 1 1 15%
  background: hotpink

.chat
  flex: 1 1 45%
  background: lightblue

.templates
  flex: 1 1 40%

/**
 *************************************** Responsive
 */
@media screen and (max-width: 990px)
  .description
    height: 60%

@media screen and (max-width: 890px)
  .description
    height: 70%

@media screen and (max-height: 320px)
  .description_form-text
    display: none

/**/